'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';
import { AlertTriangle, CheckCircle2, Clock, TrendingUp, Download } from 'lucide-react';
import MapPlaceholder from '@/components/peep/map-placeholder';
import EvidenceCard from '@/components/peep/evidence-card';

interface Report {
  id: number;
  lat: number;
  lng: number;
  category: string;
  severity: 'Low' | 'Medium' | 'High';
  note: string;
  reporter: string;
  date: string;
  zone: string;
  upvotes: number;
  verified: boolean;
  imagePlaceholder: string;
}

export default function AdminDashboard() {
  const [reports, setReports] = useState<Report[]>([]);
  const [verifiedReports, setVerifiedReports] = useState<Report[]>([]);

  useEffect(() => {
    fetch('/data/mock-reports.json')
      .then((res) => res.json())
      .then((data) => {
        setReports(data);
        setVerifiedReports(data.filter((r: Report) => r.verified));
      });
  }, []);

  const highSeverityCount = reports.filter((r) => r.severity === 'High').length;
  const pendingCount = reports.filter((r) => !r.verified).length;

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
        <button className="flex items-center gap-2 py-2 px-4 rounded-lg border border-gray-300 hover:bg-gray-100 text-sm font-medium">
          <Download className="w-4 h-4" />
          Export CSV
        </button>
      </div>

      {/* Stats cards grid */}
      <div className="grid grid-cols-4 gap-4">
        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-sm font-medium text-gray-600">Total Reports (30-day)</p>
            <TrendingUp className="w-5 h-5 text-blue-600" />
          </div>
          <p className="text-3xl font-bold text-gray-900">{reports.length}</p>
          <p className="text-xs text-gray-500 mt-2">+12% from last period</p>
        </div>

        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-sm font-medium text-gray-600">Open Red Zones</p>
            <AlertTriangle className="w-5 h-5 text-red-600" />
          </div>
          <p className="text-3xl font-bold text-gray-900">{highSeverityCount}</p>
          <p className="text-xs text-gray-500 mt-2">Require immediate attention</p>
        </div>

        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-sm font-medium text-gray-600">Pending Verifications</p>
            <Clock className="w-5 h-5 text-yellow-600" />
          </div>
          <p className="text-3xl font-bold text-gray-900">{pendingCount}</p>
          <p className="text-xs text-gray-500 mt-2">Awaiting admin review</p>
        </div>

        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-sm font-medium text-gray-600">Assigned to Authorities</p>
            <CheckCircle2 className="w-5 h-5 text-green-600" />
          </div>
          <p className="text-3xl font-bold text-gray-900">{verifiedReports.length}</p>
          <p className="text-xs text-gray-500 mt-2">In action</p>
        </div>
      </div>

      {/* Main grid: Map and recent reports */}
      <div className="grid grid-cols-3 gap-6">
        {/* Map */}
        <div className="col-span-2 bg-white rounded-lg border border-gray-200 overflow-hidden" style={{ height: '400px' }}>
          <MapPlaceholder
            pins={reports.map((r) => ({
              id: r.id,
              lat: r.lat,
              lng: r.lng,
              category: r.category,
              severity: r.severity,
            }))}
          />
        </div>

        {/* Quick stats */}
        <div className="bg-white rounded-lg border border-gray-200 p-6 space-y-4">
          <h3 className="font-semibold text-gray-900">Quick Actions</h3>

          {/* Severity breakdown */}
          <div className="space-y-2">
            <p className="text-xs font-medium text-gray-600 uppercase">Severity Breakdown</p>
            <div className="space-y-2">
              {['High', 'Medium', 'Low'].map((sev) => {
                const count = reports.filter((r) => r.severity === sev).length;
                const colors = {
                  High: { bg: '#fca5a5', text: '#7f1d1d' },
                  Medium: { bg: '#fcd34d', text: '#78350f' },
                  Low: { bg: '#c6f6d5', text: '#15803d' },
                };
                return (
                  <div
                    key={sev}
                    className="flex items-center justify-between p-2 rounded"
                    style={{ backgroundColor: colors[sev as keyof typeof colors].bg }}
                  >
                    <span
                      className="text-sm font-medium"
                      style={{ color: colors[sev as keyof typeof colors].text }}
                    >
                      {sev}
                    </span>
                    <span
                      className="text-lg font-bold"
                      style={{ color: colors[sev as keyof typeof colors].text }}
                    >
                      {count}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>

          {/* Buttons */}
          <div className="pt-2 space-y-2">
            <Link
              href="/peep/admin/evidence-table"
              className="block w-full py-2 px-4 rounded-lg text-sm font-medium text-white text-center transition-all"
              style={{ backgroundColor: 'var(--peep-primary)' }}
            >
              View All Evidence
            </Link>
            <Link
              href="/peep/admin/authority-panel"
              className="block w-full py-2 px-4 rounded-lg text-sm font-medium border border-gray-300 text-gray-700 text-center hover:bg-gray-50"
            >
              Forward to Authority
            </Link>
          </div>
        </div>
      </div>

      {/* Recent reports */}
      <div className="bg-white rounded-lg border border-gray-200 p-6">
        <h3 className="text-lg font-semibold mb-4 text-gray-900">Recent Reports</h3>
        <div className="grid grid-cols-3 gap-4">
          {reports.slice(0, 3).map((report) => (
            <EvidenceCard
              key={report.id}
              {...report}
              isAdmin={true}
              onVerify={(id) => console.log('Verify:', id)}
              onReport={(id) => console.log('Report:', id)}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
